@charset "UTF-8";
img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

/* 全域 border box */
*,
*::before,
*::after {
  box-sizing: border-box;
}

.top-10 {
  top: 10%;
}

.start-85 {
  left: 88%;
}

.iconShopSize {
  font-size: 50px;
  border: 3px solid #000;
  padding: 28px;
  border-radius: 50%;
}

.card-img-top {
  object-fit: cover;
  object-position: center center;
  height: 18.875rem;
}

.btn-primary:hover {
  background: #6a33ff;
  border: 1px solid #6a33ff;
}

.btn-link:hover {
  color: #6a33ff;
}

.tableCart {
  font-size: 20px;
}

@media screen and (max-width: 480px) {
  .tableCart {
    font-size: 14px;
  }
}

.swiper {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.hero {
  color: #ffffff;
  height: 26.25rem;
  background-image: url("https://github.com/hexschool/js-training/blob/main/%E7%AC%AC%E4%B9%9D%E9%80%B1%E4%B8%BB%E7%B7%9A%E4%BB%BB%E5%8B%99%E5%9C%96%E5%BA%AB/3ewLAKn.png?raw=true");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 100%;
}

@media screen and (max-width: 767px) {
  .hero {
    height: 20rem;
  }
}

.pdtBadge {
  width: 5.5rem;
  height: 2.75rem;
  font-size: 1.25rem;
  font-weight: normal;
  line-height: 1.5;
}

@media screen and (max-width: 767px) {
  .pdtBadge {
    width: 4rem;
    height: 2rem;
    font-size: 1rem;
    line-height: 1.3;
  }
}

.cartImg {
  width: 5rem;
  height: 5rem;
  object-fit: cover;
  object-position: center center;
}

.shippingMethod {
  position: relative;
}

.shippingMethod::after {
  position: absolute;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f0da";
  font-size: 24px;
  left: 135%;
  top: 50%;
  transform: translateY(-50%);
}

@media (max-width: 992px) {
  .shippingMethod::after {
    display: none;
  }
}
